<template>
  <div class="rank-info">
    <el-descriptions :column="isMobile ? 1 : 2" size="mini">
      <div v-if="isRank" slot="title" style="display: flex; align-items: center; justify-content: space-between;">
        <span style="font-size: 20px; color: #fff; line-height: 15px;">NO.{{rank.index}} </span>
        <a style="margin-left: 10px;" class="link-a" :href="getExplorerUrl('address', rank.account)" target="_blank">
          {{isMobile ? formatAddress(rank.account) : rank.account}}
        </a>
        <i class="el-icon-document-copy btn-icon" style="margin-left: 5px;" @click="copyText(rank.account)"></i>
        <span style="margin-left: 5px; color: #fff;">
        {{successPercent}}%
        </span>
      </div>
      <el-descriptions-item :label="$t('rank.form.title')">
        {{rank.title}}
        <i class="el-icon-document-copy btn-icon" style="margin-left: 5px;" @click="copyText(rank.title)"></i>
      </el-descriptions-item>
      <el-descriptions-item :label="$t('rank.form.content')">
        {{rank.content}}
        <i class="el-icon-document-copy btn-icon" style="margin-left: 5px;" @click="copyText(rank.content)"></i>
      </el-descriptions-item>
      <el-descriptions-item :label="$t('rank.form.successCnt')">
        <span style="color: green;" v-if="rank.successCnt > 0">{{rank.successCnt}}</span>
        <span v-else>{{rank.successCnt}}</span>
      </el-descriptions-item>
      <el-descriptions-item :label="$t('rank.form.successBalance')">
        <span style="color: green;" v-if="successBalanceInUnit > 0">{{successBalanceInUnit}} {{tokenSymbol}}</span>
        <span v-else>{{successBalanceInUnit}} {{tokenSymbol}}</span>
      </el-descriptions-item>
      <el-descriptions-item :label="$t('rank.form.failedCnt')">
        <span style="color: yellow;" v-if="rank.failedCnt > 0">{{rank.failedCnt}}</span>
        <span v-else>{{rank.failedCnt}}</span>
      </el-descriptions-item>
      <el-descriptions-item :label="$t('rank.form.failedBalance')">
        <span style="color: yellow;" v-if="failedBalanceInUnit > 0">{{failedBalanceInUnit}} {{tokenSymbol}}</span>
        <span v-else>{{failedBalanceInUnit}} {{tokenSymbol}}</span>
      </el-descriptions-item>
      <el-descriptions-item :label="$t('rank.form.arguingCnt')">
        <span style="color: red;" v-if="rank.arguingCnt > 0">{{rank.arguingCnt}}</span>
        <span v-else>{{rank.arguingCnt}}</span>
      </el-descriptions-item>
      <el-descriptions-item :label="$t('rank.form.arguingBalance')">
        <span style="color: red;" v-if="arguingBalanceInUnit > 0">{{arguingBalanceInUnit}} {{tokenSymbol}}</span>
        <span v-else>{{arguingBalanceInUnit}} {{tokenSymbol}}</span>
      </el-descriptions-item>
    </el-descriptions>
  </div>
</template>
<script>
import common from "@/utils/common"
import base from "@/utils/base"

export default {
  mixins: [base],
  name: 'RankIntro',
  props: {
    rank: Object,
    isRank: {
      type: Boolean,
      default: true
    }
  },
  components: {
  },
  computed: {
    successPercent() {
      const r = this.rank.successCnt.toNumber() / this.rank.successCnt.add(this.rank.failedCnt).add(this.rank.arguingCnt).toNumber()
      return r.toFixed(4) * 100
    },
    successBalanceInUnit() {
      const tokenBalance = this.rank.successBalance
      return common.weiToUnit(tokenBalance, this.tokenDecimals)
    },
    failedBalanceInUnit() {
      const tokenBalance = this.rank.failedBalance
      return common.weiToUnit(tokenBalance, this.tokenDecimals)
    },
    arguingBalanceInUnit() {
      const tokenBalance = this.rank.arguingBalance
      return common.weiToUnit(tokenBalance, this.tokenDecimals)
    },
  },
  data() {
    return {
    }
  },
  created() {
  },
  methods: {
  }
}
</script>
