<template>
  <sec-card class="order-panel" :show-background="true" style="height: 100%; max-width: 560px;">
    <div slot="header" class="sec-card-header">
      <div>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-jiaoyi"></use>
        </svg>
        <span style="margin-left: 10px;">{{$t("orderNew.header.title")}}</span>
      </div>
      <el-popover
          placement="bottom-end"
          :visible-arrow="false"
          v-model="showTradeSettingPopover"
          trigger="click">
        <div>
          <div style="margin-top: 5px;">
            <div>
              <span style="margin-left: 0!important;">{{$t('orderNew.form.expire')}}</span>
              <el-tooltip style="margin-left: 3px;" :content="$t('orderNew.form.expireTip')" placement="top">
                <div slot="content" v-html="expireTip"></div>
                <i class="el-icon-question"></i>
              </el-tooltip>
              <span>:</span>
            </div>
            <div>
              <el-input-number size="small" style="width: 90px;" controls-position="right" v-model="tradeForm.expireTime" :min="2" placeholder="3"></el-input-number>
              <!--            <el-input style="width: 60px;" v-model="tradeForm.expireTime" placeholder="1" oninput="value=value.replace(/[^\d]/g, '')"></el-input>-->
              <el-select size="small" v-model="tradeForm.expireTimeUnit" style="width: 90px; background-color: inherit; margin-left: 5px;">
                <el-option :label="$t('common.hour')" value="hour"></el-option>
                <el-option :label="$t('common.day')" value="day"></el-option>
                <el-option :label="$t('common.week')" value="week"></el-option>
              </el-select>
              <span style="margin-left: 5px;">{{$t('orderNew.form.expireLabel')}}</span>
            </div>
          </div>
          <div v-if="!hasInviter" style="margin-top: 5px;">
            <div>
              <span>{{$t('orderNew.form.inviter')}}</span>
              <span>:</span>
            </div>
            <div>
              <el-input type="textarea" :rows="1" autosize v-model="tradeForm.inviter" :placeholder="$t('common.optional')"></el-input>
            </div>
          </div>
        </div>
        <i slot="reference" class="el-icon-setting trade-setting-icon"></i>
      </el-popover>
    </div>
    <div>
      <div class="sec_form_panel">
        <div class="sec_form_item">
          <div class="sec_form_item_title">
            <span>{{$t('orderNew.form.payee')}}:</span>
          </div>
          <div class="sec_form_item_content">
            <el-input style="min-width: 280px;" type="textarea" :rows="1" autosize v-model="tradeForm.payee" @input="onPayeeChange" placeholder="0x"></el-input>
            <div v-if="tradeForm.payeeInfo !== null">
              <span class="tip-span" style="margin-right: 5px;">{{$t('rank.form.successBalance')}}: {{payeeSuccessBalanceInUnit}} {{tokenSymbol}}</span>
              <el-popover
                  placement="bottom"
                  :width="isMobile ? screenWidth - 20 : 500"
                  trigger="click">
                <div>
                  <div class="payee-risk-tip-panel" v-if="tradeForm.payeeInfo.arguingCnt > 0">
                    <span style="color: rgb(80, 144, 234);">
                      <b>{{$t('common.tip')}}:</b>
                    {{$t('orderNew.form.payeeRishTip')}}
                  </span>
                  </div>
                  <rank-intro style="margin-top: 5px;" :is-rank="false" :rank="tradeForm.payeeInfo"></rank-intro>
                </div>
                <i slot="reference" class="el-icon-question" :style="tradeForm.payeeInfo.arguingCnt > 0 ? 'color: #fa4d56' : ''"></i>
              </el-popover>
            </div>
          </div>
        </div>
        <div style="display: flex; justify-content: center; align-items: center; padding: 20px 0 5px 0; line-height: 15px;">
          <span class="tip-span">{{$t('orderNew.form.balance')}}: {{tokenBalanceInUnit}} {{tokenSymbol}}</span>
          <el-button type="text" style="margin-left: 5px; padding: 0;" @click="goMax">{{$t('common.max')}}</el-button>
        </div>
        <div style="border-radius: 10px; border: 1px dashed #5d5d5d; padding: 10px; margin-bottom: 10px;">
          <div class="sec_form_item">
            <div class="sec_form_item_title">
              <span>{{$t('orderNew.form.amount')}}:</span>
            </div>
            <div class="sec_form_item_content">
              <amount-input v-model="tradeForm.amount" :symbol="tokenSymbol" :decimals="tokenDecimals" @change="onAmountChange"></amount-input>
              <span class="tip-span">{{$t('orderNew.form.actuallyArrived')}} {{orderActuallyArrived}} {{tokenSymbol}}</span>
            </div>
          </div>
          <div class="sec_form_item">
            <div class="sec_form_item_title">
              <span style="margin-left: 0!important;">{{$t('orderNew.form.margin')}}</span>
              <el-tooltip style="margin-left: 3px;" :content="$t('orderNew.form.marginTip')" placement="top">
                <div slot="content" v-html="marginTip"></div>
                <i class="el-icon-question"></i>
              </el-tooltip>
              <span>:</span>
            </div>
            <div class="sec_form_item_content">
              <!--            <div>-->
              <!--              <span style="font-size: 13px;line-height: 20px;"> {{$t('orderNew.form.marginBalance')}}: {{marginBalanceInUnit}} {{tokenSymbol}}</span>-->
              <!--              <el-button style="padding: 0!important; margin-left: 5px;" type="text" @click="goDepositMargin">{{$t('orderNew.form.marginBalanceDeposit')}}</el-button>-->
              <!--            </div>-->
              <span class="tip-span" style="margin-left: 0!important;">{{$t('orderNew.form.margin')}} {{margin}} {{tokenSymbol}} ({{tradeForm.marginPercent}}%)</span>
              <el-popover
                  style="margin-left: 5px;"
                  placement="bottom"
                  :visible-arrow="false"
                  v-model="showMarginSettingPopover"
                  trigger="click">
                <div>
                  <div>
                    <div>
                      <span style="margin-left: 0!important;">{{$t('orderNew.form.margin')}}</span>
                      <el-tooltip style="margin-left: 3px;" :content="$t('orderNew.form.marginTip')" placement="top">
                        <div slot="content" v-html="marginTip"></div>
                        <i class="el-icon-question"></i>
                      </el-tooltip>
                      <span>:</span>
                      <span>{{$t('orderNew.form.margin')}} {{margin}} {{tokenSymbol}}</span>
                    </div>
                    <div>
                      <div style="display: flex; align-items: center; flex-wrap: wrap;">
                        <el-radio-group style="margin-right: 5px;" size="mini" v-model="tradeForm.marginLevel"  @change="onMarginLevelChange">
                          <el-radio-button v-for="(item, index) in marginPercentList" :key="index" :label="item.label">{{$t('orderNew.form.marginRiskLevel.' + item.label)}}</el-radio-button>
                        </el-radio-group>
                        <amount-input style="width: 70px;" class="margin-input" v-model="tradeForm.marginPercent" @change="onMarginPercentChange" :line-height="18" :decimals="2" symbol="%"></amount-input>
                        <!--              <span>{{tradeForm.marginPercent}}%</span>-->
                      </div>
                    </div>
                  </div>
                </div>
                <el-button slot="reference" type="text" @click="goMarginCustomize" style="padding: 0;">{{$t('common.customize')}}</el-button>
              </el-popover>
            </div>
          </div>
        </div>
      </div>

      <div style="width: 100%; margin-top: 20px; text-align: center;">
        <div style="display: flex; align-items: center; justify-content: center;">
          <span class="tip-span">{{$t('orderNew.form.expectedDToken')}}: {{expectedDTokenInUnit}} {{dtokenSymbol}}</span>
          <el-tooltip style="margin-left: 5px;" :content="$t('orderNew.form.expectedDTokenTip')" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>
        </div>
        <tx-commit-button style="margin-top: 5px;" :tx-loading="txLoading" :tx-form-check="txFormCheck"></tx-commit-button>
      </div>
    </div>

    <el-dialog
        :visible.sync="txConfirmDialogVisible"
        custom-class="wallet-dialog"
        :title="$t('common.txConfirmDialogTitle')"
        @close="onTxConfirmDialogClose"
        width="90%">
      <tx-confirm ref="txConfirm" v-model="hash" :tx-form="txForm" :add-token="addToken" :call-func="txConfirmCall">
        <div style="width: 100%;" slot="content">
          <div style="width: 100%; text-align: center;" v-if="needApprove">
            {{$t('common.approve')}} {{tokenSymbol}}
          </div>
          <div v-else class="sec_form_panel">
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>
                  {{$t('orderNew.form.payee')}}:
                </span>
              </div>
              <div class="sec_form_item_content">
                {{tradeForm.payee}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>
                  {{$t('orderNew.form.amount')}}:
                </span>
              </div>
              <div class="sec_form_item_content">
                {{tradeForm.amount}} {{tokenSymbol}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>
                  {{$t('orderNew.form.fee')}}:
                </span>
              </div>
              <div class="sec_form_item_content">
                {{orderFeeInUnit}} {{tokenSymbol}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>
                  {{$t('orderNew.form.actuallyArrived')}}:
                </span>
              </div>
              <div class="sec_form_item_content">
                {{orderActuallyArrived}} {{tokenSymbol}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>
                  {{$t('orderNew.form.margin')}}:
                </span>
              </div>
              <div class="sec_form_item_content">
                {{margin}} {{tokenSymbol}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>
                  {{$t('orderNew.form.expectedDToken')}}:
                </span>
              </div>
              <div class="sec_form_item_content">
                {{expectedDTokenInUnit}} {{dtokenSymbol}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>
                  {{$t('orderNew.form.expire')}}:
                </span>
              </div>
              <div class="sec_form_item_content">
                {{tradeForm.expireTime}} {{$t('common.' + tradeForm.expireTimeUnit)}}
                <span>{{$t('orderNew.form.expireLabel')}}</span>
              </div>
            </div>
            <div v-if="!hasInviter && tradeForm.inviter !== ''" class="sec_form_item">
              <div class="sec_form_item_title">
                <span>
                  {{$t('orderNew.form.inviter')}}:
                </span>
              </div>
              <div class="sec_form_item_content">
                {{txForm.inviter}}
              </div>
            </div>
          </div>
        </div>
        <span slot="info" style="font-size: 20px;">
          {{ needApprove ? $t('common.approve') + tokenSymbol : formatTradeInfo}}
        </span>
      </tx-confirm>
    </el-dialog>
  </sec-card>
</template>
<script>
import TxCommitButton from '@/components/common/tx_commit_button'
import AmountInput from '@/components/common/amount_input'
import RankIntro from '@/components/rank/rank_intro'
import common from '@/utils/common'
import TxConfirm from '../tx/tx_confirm'
import SecCard from '../sec_card'
import erc20 from "@/utils/erc20"
import pool from "@/utils/pool"
import dtoken from "@/utils/dtoken"
import events from "@/utils/events"
import base from "@/utils/base"
import db from "@/utils/db"
import tx_base from "@/utils/tx_base"
import factory from "@/utils/factory";
export default {
  mixins: [base, tx_base],
  name: 'OrderNew',
  props: {
  },
  components: {
    SecCard,
    TxConfirm,
    RankIntro,
    TxCommitButton,
    AmountInput
  },
  computed: {
    payeeSuccessBalanceInUnit() {
      if (null === this.tradeForm.payeeInfo) {
        return 0
      }
      const tokenBalance = this.tradeForm.payeeInfo.successBalance
      return common.weiToUnit(tokenBalance, this.tokenDecimals)
    },
    amountInWei() {
      const amountInWei = common.unitToWei(this.tradeForm.amount.toString())
      return amountInWei
    },
    marginInWei() {
      return this.getMarginFromPercent()
    },
    margin() {
      if (this.amountInWei.lte(common.toBigNumber(0))) {
        return 0
      }
      return common.weiToUnit(this.marginInWei)
    },
    expireTip() {
      return this.$t('orderNew.form.expireTip')
    },
    marginTip() {
      return this.$t('orderNew.form.marginTip')
    },
    hasInviter() {
      return this.factoryInviter !== common.getEmptyAddress()
    },
    marginBalanceInUnit() {
      const marginBalance = this.tradeInfo.marginBalance
      return common.weiToUnit(marginBalance)
    },
    payeeMinAmountInUnit() {
      const payeeMinAmount = this.tradeForm.payeeMinAmount
      return common.weiToUnit(payeeMinAmount)
    },
    expectedDTokenInUnit() {
      const tokenBalance = this.tradeForm.expectedDToken
      if (tokenBalance === '') {
        return 0
      }
      return common.weiToUnitWithDecimals(tokenBalance, this.dtokenDecimals)
    },
    needApprove() {
      if (this.tokenAllowance === null) {
        return false
      }
      if (this.tradeForm.amount === '' || !this.tradeForm.amount) {
        return false
      }
      if (this.tokenAllowance.lt(common.unitToWei(this.tradeForm.amount.toString()))) {
        return true
      } else {
        return false
      }
    },
    formatTradeInfo() {
      return `${this.$t('orderNew.header.title')} ${this.tradeForm.amount} ${this.tokenSymbol}`
    },
    tradeAmountInWei() {
      return this.amountInWei.add(this.marginInWei)
    },
    orderFee() {
      if (null === this.$store.state.factory.config) {
        return 0
      }
      if (this.tradeForm.amount === '') {
        return 0
      }
      let wei = common.unitToWei(this.tradeForm.amount).mul(this.$store.state.factory.config.fee).div(common.toBigNumber(1000))
      return wei
    },
    orderFeeInUnit() {
      return common.weiToUnit(this.orderFee)
    },
    orderActuallyArrived() {
      if (this.tradeForm.amount === 0 || this.tradeForm.amount === '') {
        return 0
      }
      return (this.tradeForm.amount - this.orderFeeInUnit).toFixed(this.tokenDecimals)
    },
    showInviter() {
      if (this.hasInviter) {
        return false
      }
      if (this.tradeForm.inviter === null || this.tradeForm.inviter === '' || this.tradeForm.inviter === common.getEmptyAddress()) {
        return false
      }
      return true
    }
  },
  data() {
    return {
      init: false,
      marginPercentList: [
        {
          min: 0,
          max: 20,
          value: 10,
          label: 'week'
        },
        {
          min: 20,
          max: 40,
          value: 25,
          label: 'medium'
        },
        {
          min: 40,
          max: 70,
          value: 50,
          label: 'high'
        },
        {
          min: 70,
          max: 100,
          value: 75,
          label: 'veryHigh'
        }
      ],
      showTradeSettingPopover: false,
      showMarginSettingPopover: false,
      tradeForm: {
        payeeInfo: null,
        payee: '',
        payeeMinAmount: 0,
        amount: '',
        marginLevel: 'medium',
        showMarginPercent: false,
        marginPercent: 25,
        expectedDToken: '',
        expireTime: 3,
        expireTimeUnit: 'hour',
        inviter: '',
      },
      txConfirmDialogVisible: false,
    }
  },
  watch: {
  },
  created() {
    this.eventHandlers[events.tokenChanged] = () => {
      this.initData()
    }
    this.eventHandlers[events.txSent] = param => {
      if (this.hash === param && this.txForm.type === common.getTxTypes().create) {
        this.tradeForm.payee = ''
        this.tradeForm.payeeInfo = null
        this.tradeForm.amount = ''
        this.tradeForm.expectedDToken = ''
        this.tradeForm.inviter = ''
      }
    }
    this.eventHandlers[events.txConfirmed] = async param => {
      if (param.type === common.getTxTypes().approve
          || param.type === common.getTxTypes().create
          || param.type === common.getTxTypes().delivered
          || param.type === common.getTxTypes().frozeOrder
          || param.type === common.getTxTypes().confirmReceived
          || param.type === common.getTxTypes().withdrawTimeoutOrder) {
        await erc20.syncTokenBalance()
        const inviter = await factory.getInviter(this.account)
        await this.$store.commit('setFactoryInviter', inviter)
        if (this.hash === param.hash) {
          this.txPending = false
        }
      }
    }
  },
  methods: {
    baseInit() {
      this.initData()
    },
    initData() {
      const setting = this.$store.state.tradeInfo.setting
      if (null !== setting) {
        this.tradeForm.expireTime = setting.et
        this.tradeForm.expireTimeUnit = setting.eu
        this.tradeForm.marginPercent = setting.mp
        this.onMarginPercentChange()
      }
      if (!this.hasInviter) {
        this.tradeForm.inviter = this.$store.state.tradeInfo.inviter
      }
      this.onPayeeChange(this.tradeForm.payee)
      this.onAmountChange(this.tradeForm.amount)
      this.init = true
    },
    goMarginCustomize() {
      this.showTradeSettingPopover = true
    },
    onTxConfirmDialogClose() {
      if (this.txForm.type === common.getTxTypes().create && this.hash !== '') {
        window.scrollTo({
          top: 590,
          left: 0,
          behavior: 'smooth'
        })
      }
    },
    txFormCheck() {
      let r = {
        success: false
      }
      if (this.tradeForm.payee === '') {
        r.msg = this.$t('orderNew.btn.inputPayeeAddress')
        return r
      }
      if (null === common.getAddress(this.tradeForm.payee)) {
        r.msg = this.$t('orderNew.btn.payeeAddressError')
        return r
      }
      if (this.amountInWei <= 0) {
        r.msg = this.$t('orderNew.btn.inputAmount')
        return r
      }
      if (this.tradeAmountInWei.gt(this.tokenBalance)) {
        r.msg = this.$t('common.balanceNotEnough')
        return r
      }
      if (this.amountInWei.lt(this.tradeForm.payeeMinAmount)) {
        r.msg = this.$t('orderNew.btn.lessThanPayeeMinAmount')
        return r
      }
      if (this.tradeForm.inviter !== '' && null === common.getAddress(this.tradeForm.inviter)) {
        r.msg = this.$t('orderNew.btn.inviterAddressError')
        return r
      }
      if (this.needApprove) {
        r.success = true
        r.msg = this.$t('common.approve')
        r.func = this.goSell
        return r
      }
      r.success = true
      r.msg = this.$t('orderNew.btn.buy')
      r.func = this.goSell
      return r
    },
    goMax() {
      this.tradeForm.amount = this.getMaxAmount()
      this.onAmountChange(this.tradeForm.amount)
    },
    getMaxAmount() {
      const wei = this.tokenBalance.mul(common.toBigNumber(100)).div(common.toBigNumber(100).add(common.toBigNumber(this.tradeForm.marginPercent)))
      return common.weiToUnit(wei)
    },
    async onAmountChange(val) {
      if (val === '') {
        return
      }

      // this.setMarginFromPercent()
      if (!this.txLoading && this.$store.state.dtoken !== null) {
        const getDtoken = await dtoken.tryMint(this.orderFee)
        this.tradeForm.expectedDToken = getDtoken
      }
    },
    getMarginFromPercent() {
      let p = 0
      if (this.tradeForm.marginPercent !== '') {
        p = this.tradeForm.marginPercent
      }
      if (p === 0) {
        return common.toBigNumber(0)
      }
      const marginInWei = this.amountInWei.mul(common.toBigNumber(p)).div(common.toBigNumber(100))
      return marginInWei
    },
    onMarginLevelChange(val) {
      for (let i = 0; i < this.marginPercentList.length; i++) {
        if (val === this.marginPercentList[i].label) {
          this.tradeForm.marginPercent = this.marginPercentList[i].value
          if (this.tradeAmountInWei.gt(this.tokenBalance)) {
            this.$message.info(this.$t('common.balanceNotEnough'))
          }
        }
      }
    },
    onMarginPercentChange() {
      this.tradeForm.marginLevel = this.getMarginLevelFromPercent()
      if (this.tradeAmountInWei.gt(this.tokenBalance)) {
        this.$message.info(this.$t('common.balanceNotEnough'))
      }
    },
    getMarginLevelFromPercent() {
      for (let i = 0; i < this.marginPercentList.length; i++) {
        if (this.tradeForm.marginPercent >= this.marginPercentList[i].min
            && this.tradeForm.marginPercent < this.marginPercentList[i].max) {
          return this.marginPercentList[i].label
        }
      }

      return this.marginPercentList[this.marginPercentList.length - 1].label
    },
    async onPayeeChange(val) {
      this.tradeForm.payeeInfo = null
      const payee = common.getAddress(val)
      if (null !== payee && null !== this.poolItem) {

        const minAmount = await pool.payeeLimits(payee)
        this.tradeForm.payeeMinAmount = minAmount
        if (this.amountInWei.lt(this.tradeForm.payeeMinAmount)) {
          this.tradeForm.amount = this.payeeMinAmountInUnit
        }

        const r = await pool.payeeInfos(payee)
        this.tradeForm.payeeInfo = {
          account: r.account,
          title: common.hexToString(r.title),
          content: common.hexToString(r.content),
          successCnt: r.sucCnt,
          successBalance: r.sucBal,
          failedCnt: r.failCnt,
          failedBalance: r.failBal,
          arguingCnt: r.argCnt,
          arguingBalance: r.argBal,
        }
      }
    },
    getWindowHour() {
      switch (this.tradeForm.expireTimeUnit) {
        case 'hour':
          return this.tradeForm.expireTime
        case 'day':
          return this.tradeForm.expireTime * 24
        case 'week':
          return this.tradeForm.expireTime * 24 * 7
      }
      return 3
    },
    async goSell() {

      const payee = common.getAddress(this.tradeForm.payee)
      if (null === payee) {
        this.$message.warning(this.$t('orderNew.btn.payeeAddressError'))
        return
      }

      if (this.tradeForm.amount < this.payeeMinAmountInUnit) {
        this.$message.warning('小于买家的最小金额')
        return
      }

      let inviter = this.tradeForm.inviter
      if (this.hasInviter) {
        inviter = common.getEmptyAddress()
      } else if (inviter === '') {
        inviter = this.account
      } else {
        inviter = common.getAddress(this.tradeForm.inviter)
        if (null === inviter) {
          this.$message.warning(this.$t('orderNew.btn.payeeAddressError'))
          return
        }
      }

      if (this.tradeAmountInWei.gt(this.tokenBalance)) {
        this.$message.warning(this.$t('common.balanceNotEnough'))
        return
      }

      if (this.$refs['txConfirm']) {
        this.$refs['txConfirm'].init()
      }

      this.hash = ''

      this.addToken = null

      if (this.needApprove) {
        this.txForm = {
          type: common.getTxTypes().approve,
          token: this.tokenItem.address,
          to: this.tokenItem.address,
          sender: this.account,
        }
        this.txConfirmCall = () => {
          return erc20.approve(this.poolItem.address, -1)
        }
      } else {
        let blockInterval = common.getNetVersionBlockInterval()
        if (blockInterval === 0) {
          blockInterval = 13
        }
        const windowHeight = Math.floor(this.getWindowHour() * 3600 / blockInterval)

        this.txForm = {
          type: common.getTxTypes().create,
          to: this.poolItem.address,
          payer: this.account,
          payee: payee,
          amount: common.unitToWei(this.tradeForm.amount, this.tokenItem.decimals),
          margin: this.marginInWei,
          windowHour: this.getWindowHour(),
          windowHeight: windowHeight,
          inviter: inviter,
          fee: this.orderFee,
          dToken: this.tradeForm.expectedDToken,
        }
        this.txConfirmCall = () => {
          return pool.create(
              payee,
              this.amountInWei,
              this.marginInWei,
              this.getWindowHour(),
              windowHeight,
              inviter)
        }
        this.addToken = {
          address: this.dtokenAddress,
          symbol: this.dtokenSymbol,
          decimals: this.dtokenDecimals,
        }
        await db.updateSetting({
          mp: this.tradeForm.marginPercent,
          et: this.tradeForm.expireTime,
          eu: this.tradeForm.expireTimeUnit,
        })
      }

      this.txConfirmDialogVisible = true
    },
  }
}
</script>
